@import "mixins";
@import "fonts";
@import "vendor";
@import "vars";
@import "settings";

* {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 25px;    
    text-decoration: none;
    color: #000;
}
html {
    overflow-x: hidden;
}

.container {
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 16px;
}
body {
    position: relative;
}
._tab-active {
    .plan__tabs-list__item__radial {
        background: #fff;
    }
    .plan__tabs-list__item__radial__inner {
        background: #333333 !important;
    }
}

.burger {
    width: 24px;
    height: 14px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    display: none;       

    .burger__item {
        width: 100%;
        height: 2px;
        background: #AC835E;
        border-radius: 5px; 
    }
}
.mobile__nav {
    opacity: 0;
    transform: translateY(-1000px);
    transition: all 0.3s ease-in-out;

    &.mobile__nav__active {
        opacity: 1;
        transform: translateY(0);
    }

    width: 100vw;
    box-sizing: border-box;
    background: #333333;
    padding: 125px 15px 45px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99;
    .mobile__nav__list {

    }
    .mobile__nav__list__item {

    }
    .mobile__nav__list__item__link {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 27px;
        line-height: 60px;
        /* or 222% */
        text-transform: uppercase;
        color: #fff;        
    }
    .mobile__nav__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 45px;
        span {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 17px;
            /* identical to box height */
            color: #FFFFFF;
            text-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);            
        }
    }
}
.header {
    position: absolute;
    z-index: 100;
    top: 0;
    left: 0;
    width: 99vw;
    box-sizing: border-box;
    padding: 15px 0 14px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .nav {
        font-family: 'Proxima Nova';        
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .nav__item {
        &:not(:last-child) {
            margin-right: 40px;
        }
        &:hover .nav__item__link {
            color: #FFD2AA;
        }
    }
    .nav__item__link {
        font-size: 14px;
        line-height: 17px;
        font-family: 'Proxima Nova';
        color: #FFFFFF;
    }
    .phone {
        font-size: 14px;
        line-height: 17px;
        font-family: 'Proxima Nova';
        color: #FFFFFF;
    }
}

.title {
    background-image: url('../img/title.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 164px 0 90px;
    position: relative;

    margin-bottom: 185px;
    .container {

    }
    .title__text {
        max-width: 863px;
        margin-bottom: 133px;
    }
    .title__text__heading {
        font-weight: 500;
        font-size: 44px;
        line-height: 54px;
        
        color: #FFFFFF;
        
        text-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
        margin-bottom: 15px;
    }
    .title__text__span {
        font-weight: 500;
        font-size: 24px;
        line-height: 29px;
        
        color: #FFFFFF;
        
        text-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);       
    }
    .title__link {
        font-weight: 600;
        font-size: 16px;
        line-height: 27px;
        padding: 9px 35px;
        display: inline-block;
        color: #FFFFFF;
        background: #AC835E; 
        margin-bottom: 72px;        
    }
    .title__info-list {
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .title__info-list__item {
        font-weight: 500;
        font-size: 18px;
        color: #FFFFFF;
        
        &:not(:last-child) {
            margin-right: 35px;
        }
        span {
            font-weight: 500;
            font-size: 44px;
            line-height: 54px;
            /* identical to box height */
            color: #FFD2AA;            
        }
    }
    .title__scroll-link {
        display: block;
        color: #929292;
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 27px; 

        position: absolute;
        bottom: -40px;
        left: 50%;
        transform: translateX(-88.5px);
        width: 177px;
        box-sizing: border-box;
        text-align: center;
    }
}

.about {
    margin-bottom: 195px;
    .container {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 100px;
        row-gap: 207px;
        align-items: flex-start;
    }
    .about__wrapper {

    }
    .about__wrapper__list {

    }
    .about__wrapper__list__item {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;        

        display: flex;
        justify-content: start;
        align-items: flex-start;
        flex-direction: column;

        border-bottom: 1px solid #D0D0D0;

        &:first-child {
            font-family: 'Proxima Nova';
            text-transform: uppercase;
            color: #000000;   
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 19px; 
            padding-bottom: 30px;                    
        }
        &:not(:first-child) {
            padding: 30px 0;
        }
        span {
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 17px;
            margin-bottom: 7px;     
        }
    }
    .about__wrapper__heading {
        font-style: normal;
        font-weight: 400;
        font-size: 34px;
        line-height: 60px; 
        margin-bottom: 58px;  
        span {
            display: block;
            font-size: 54px;
            line-height: 60px;  
            text-transform: uppercase;
        }    
    }
    .about__wrapper__desc {
        
    }
    .about__wrapper__ac-list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: repeat(4, auto);
        align-items: start;
        align-content: start;
        column-gap: 59px;
        row-gap: 20px;
        grid-auto-flow: column;
    }
    .about__wrapper__ac-list__item {

    }
    .about__wrapper__ac-list__item__title {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .about__wrapper__ac-list__item__title__heading {
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 70px;
        
        strong {
            font-size: 40px;
            color: #AC835E;
        }
    }
    .about__wrapper__ac-list__item__title__sign {
        font-size: 30px;
    }
    .about__wrapper__ac-list__item__content {
        border-top: 1px solid #D0D0D0;
        border-bottom: 1px solid #D0D0D0;
        padding: 10px 0;
    }
}

.plan {
    background: #333333;
    padding: 29px 0 20px;
    .container {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 57px;
        align-items: flex-end;
    }
    .plan__info-wrap {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-direction: column;
    }
    .plan__info-wrap__text-container {
        p {
            color: #FFFFFF;
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 25px;                       
        }
    }
    .plan__info-wrap__heading {
        color: #C99C73;  
        font-weight: 400;
        font-size: 54px;
        line-height: 60px;
        margin-bottom: 70px;
        
        span {
            color: #FFFFFF;
            font-family: 'Proxima Nova';
            font-weight: 600;
            font-size: 16px;
            line-height: 19px;
            display: block;                        
        }
    }
    .plan__tabs-list {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 273px;
        width: 100%;
    }
    .plan__tabs-list__item {
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        span {
            font-weight: 500;
            font-size: 16px;
            line-height: 25px;
            /* identical to box height, or 156% */
            color: #FFFFFF;  
            margin-top: 10px;      
        }
    }
    .plan__tabs-list__item__radial {
        width: 20px;
        height: 20px;
        box-sizing: border-box;
        border: 2px solid #FFFFFF;
        border-radius: 50%; 
        
        display: flex;
        justify-content: center;
        align-items: center;      
    }
    .plan__tabs-list__item__radial__inner {
        width: 4px;
        height: 4px;
        box-sizing: border-box;
        background: #fff;
        border-radius: 50%;
    }
    .plan__slider-wrap {
        
    }
    .plan__main-slider {
        max-width: 670px;
        height: 100%;
    }
    .plan__main-slider__item {
        object-fit: cover;
    }
    .plan__bot-slider {
        max-width: 670px;
    }
    .plan__bot-slider__item {
        width: 129px;
        height: 68px;
        border: 2px solid #FFFFFF;
        &:not(:last-child) {
            margin-right: 5px;
        }
        img {
            height: inherit;
            width: inherit;
            object-fit: cover;
            object-position: center;
        }
    }
}

.video {
    margin-bottom: 200px;
    height: 550px;
    background: #E7D6C6;
    text-align: center;
    padding-top: 200px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 60px;
    
    /* or 111% */
    text-transform: uppercase;
    
    color: #B2875F;    
}

.placement {
    margin-bottom: 73px;
    .container {

    }
    .placement__heading {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 30px;
        align-items: flex-end;
        margin-bottom: 92px;
    }
    .placement__head {
        font-weight: 400;
        font-size: 54px;
        line-height: 60px;
        color: #B2875F;
    }
    .placement__desc {
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        
        color: #000000;

        strong {
            color: #000000;
            font-weight: 600;
            font-size: 16px;
            line-height: 19px;
            display: block;
            margin-bottom: 10px;                        
        }
    }
    .placement__wrapper {
        display: grid;
        grid-template-columns: 3fr 4fr;
        column-gap: 100px;
        align-items: flex-start;
    }
    #map {
        height: 400px;
    }
    .placement__tabs {

    }
    .placement__tabs__item {
        font-weight: 600;
        font-size: 20px;
        line-height: 24px;  
        color: #000000;
        
        &:not(:last-child) {
            padding-bottom: 30px;
            border-bottom: 1px solid #D0D0D0;
            margin-bottom: 30px;
        }
    }
}

.environment {
    padding: 127px 0;
    background-image: url('../img/environment-bg.jpg');
    background-position: 0 188px;
    background-repeat: no-repeat;
    margin-bottom: 73px;

    // .environment__bg {
    //     width: 99vw;
    //     object-fit: cover;
    //     position: absolute;
    //     top: 50%;
    //     transform: translateY(-342px);
    //     height: 684px;
    //     z-index: 0;
    // }
    .container {
        position: relative;
        z-index: 1;
        display: flex;
        justify-content: start;
        // align-items: flex-start;
        flex-direction: column;
    }
    .environment__heading {
        font-weight: 400;
        font-size: 54px;
        line-height: 60px;        
        color: #B2875F;
        margin-bottom: 240px;
        text-transform: uppercase;

        span {
            color: #fff;
        }
    }
    .environment__desc {
        font-style: normal;
        font-weight: 400;
        font-size: 40px;
        line-height: 40px;
        max-width: 370px;
        align-self: flex-end;
        margin-bottom: 78px;
        color: #FFFFFF;
    }
    .environment__wrapper {
        position: relative;
        display: flex;
        justify-content: start;
        flex-direction: column;
    }
    .environment__tabs {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 40px;
        width: 100%;
    }
    .environment__tabs__item {
        color: #FFFFFF;
        text-shadow: 0px 0px 15px #000000; 
        font-weight: 600;
        font-size: 16px;
        line-height: 150%;               
    }
    .environment__slider {
        width: 1065px;
    }
    .environment__slider__item {
        display: flex;
        justify-content: start;
    }
    .environment__slider__item__img-box {
        max-width: 400px;
        width: 100%;
        height: 400px;
        box-sizing: border-box;
    }
    .environment__slider__item__wrap {
        width: 585px;
        padding: 19px 40px;
        background: #B3875F;
        color: #fff;
    }
    .environment__slider__item__wrap__top {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
    .environment__slider__item__wrap__top__time {
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;     
        color: #fff;
        
        strong {
            font-weight: 300;
            font-size: 150px;
            line-height: 150px;
            display: block;
            color: #fff;
        }
    }
    .environment__slider__item__wrap__name {
        font-weight: 300;
        font-size: 22px;
        line-height: 27px;        
        color: #fff;
        text-align: right;
    }
    .environment__slider__controls {
        position: absolute;
        bottom: 44px;
        right: 130px;
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .environment__slider__controls__button {
        border: none;
        background: none;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .env-next {
        transform: scale(-1);
        margin-left: 20px;
    }
}

.documents {
    margin-bottom: 200px;
    .container {

    }
    .documents__heading {
        color: #B2875F;
        font-style: normal;
        font-weight: 400;
        font-size: 54px;
        line-height: 60px;
        text-transform: uppercase; 
        text-align: center; 
        margin-bottom: 80px;              
    }
    .documents__wrapper {

    }
    .documents__wrapper__item {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        border-bottom: 1px solid #D0D0D0;
        padding-bottom: 45px;
        &:not(:last-child) {
            margin-bottom: 45px;
        }
    }
    .documents__wrapper__item__wrap {
        &:first-child {
            display: flex;
            justify-content: start;
            align-items: center;
        }
    }
    .documents__wrapper__item__icon-box {
        border: 1px solid #B2875F;
        box-sizing: border-box;
        width: 60px;
        height: 60px;    
        border-radius: 50%; 
        display: flex;
        justify-content: center;
        align-items: center;  
        margin-right: 40px;
    }
    .documents__wrapper__item__text-box {

    }
    .documents__wrapper__item__text {
        display: block;
        text-transform: uppercase;
        color: #000000;
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
        font-family: 'Proxima Nova';
        &:not(:last-child) {
            margin-bottom: 30px;
        }                
    }
    .documents__wrapper__item__desc {
        display: block;
        text-align: right;
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
        font-family: 'Proxima Nova';
        color: #D0D0D0;                
        &:not(:last-child) {
            margin-bottom: 30px;
        } 
    }
}
.contacts {
    background: #333333;
    padding: 84px 0 58px;
    .container {

    }
    .order {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 30px;
        align-items: flex-start;
        margin-bottom: 70px;
    }
    .order__heading {
        color: #C99C73;
        font-weight: 400;
        font-size: 54px;
        line-height: 60px; 
        text-transform: uppercase;       
    }
    .order__wrap {
        display: flex;
        justify-content: start;
        align-items: flex-start;
        flex-direction: column;
    }
    .order__wrap__input {
        width: 100%;
        background: none;
        border: none;
        border-bottom: 1px solid rgba(255, 255, 255, 0.8);
        padding: 0 0 8px;
        outline: none;

        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 150%; 
        color: #fff;  
        margin-bottom: 30px;     
    }
    .order__wrap__label {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        /* identical to box height, or 143% */
        color: #FFFFFF; 
        margin-bottom: 34px;       
    }
    .order__wrap__submit {
        padding: 9px 35px;
        background: none;
        border: 2px solid #C99C73; 
        color: #C99C73; 
        font-weight: 600;
        font-size: 16px;
        line-height: 27px;              
    }
    .contacts__wrap {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 156px;
    }
    .contacts__wrap__container {
        display: flex;
        justify-content: start;
        align-items: flex-start;
        flex-direction: column;
    }
    .contacts__wrap__container__desc {
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        text-transform: uppercase;
        margin-bottom: 20px;
        color: #FFFFFF;
    }
    .contacts__wrap__container__text {
        font-weight: 400;
        font-size: 32px;
        line-height: 39px;
        
        color: #FFFFFF;
    }
    .contacts__wrap__container__link {
        font-style: normal;
        font-weight: 400;
        font-size: 32px;
        line-height: 39px;
        
        color: #FFFFFF;
    }
}
.footer {
    padding: 20px 0;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    background: #333333;
    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .footer__list {
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .footer__list__item {
                
        &:not(:last-child) {
            margin-right: 45px;
        }
    }
    .footer__list__item__link {
        font-family: 'Montserrat';
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        color: #FFFFFF;
    }
    .copy {
        font-family: 'Proxima Nova';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
        
        color: #FFFFFF;
    }
}

@media screen and (max-width: 1170px) {
    .about .about__wrapper__ac-list {
        grid-template-columns: 1fr;
        grid-auto-flow: row;
    }
    .plan .container {
        grid-template-columns: 1fr;
    }
    .plan .plan__tabs-list {
        max-width: 670px;
        margin-top: 45px;
        margin-bottom: 45px;
    }
    .plan .plan__info-wrap__heading {
        margin-bottom: 30px;
    }
}

@media screen and (max-width: 870px) {
    .burger {
        display: flex;
    }
    .header .nav {
        display: none;
    }
    .about .container {
        grid-template-columns: 1fr;
        row-gap: 100px;
    }
    .title {
        margin-bottom: 100px;
    }
    .about .about__wrapper__heading, .about .about__wrapper__heading * {
        color: #B2875F;
    }
    .about .about__wrapper__heading {
        margin-bottom: 30px;
    }
    .plan .plan__bot-slider {
        display: none;
    }
}
@media screen and (max-width: 705px) {
    .plan .plan__main-slider {
        width: 290px;
        height: 290px;
    }
    .plan .plan__main-slider__item {
        width: 290px;
        height: 290px;
        img {
            width: 290px;
            object-fit: cover;
        }
    }
    .video {
        margin-bottom: 114px;
    }
    .placement .placement__heading {
        grid-template-columns: 1fr;
        row-gap: 40px;
    }
    .placement .placement__wrapper {
        grid-template-columns: 1fr;
        row-gap: 30px;
    }
    .placement__tabs {
        display: flex;
        justify-content: start;
        align-items: center;
        flex-direction: row;
        overflow-y: hidden;
        overflow-x: scroll;
    }
    .placement .placement__tabs__item:not(:last-child) {
        padding: 0;
        margin-bottom: 0;
        border-bottom: 0;
        margin-right: 40px;
    }
    .placement .placement__tabs__item {
        white-space: nowrap;
    }
    .placement #map {
        width: 290px;
        height: 290px;
    }
    .environment .environment__desc {
        display: none;
    }
    .environment .environment__slider__item__img-box {
        display: none;
    }
    .environment .environment__slider__item__wrap {
        width: 100%;
        align-self: stretch;
    }
    .environment .environment__wrapper {
    }
    .environment .environment__slider {
        align-self: stretch;
    }
    .environment .environment__tabs {
        overflow: auto;
    }
    .environment .environment__tabs__item {
        white-space: nowrap;

        &:not(:last-child) {
            margin-right: 25px;
        }
    }
    .environment {
        padding: 127px 0 0;
    }
    .placement {
        margin-bottom: 0;
    }
    .documents {
        margin-bottom: 50px;
    }
    .contacts .order {
        grid-template-columns: 1fr;
        row-gap: 40px;
    }
    .contacts .contacts__wrap {
        grid-template-columns: 1fr;
        row-gap: 25px;
    }
    .footer .footer__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        row-gap: 20px;
        grid-template-rows: auto auto auto auto;
